* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  color: #444;
  margin: 0;
}

#head {
  animation: fadein 0.5s ease-in 0s backwards;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#mid {
  animation: slidein 0.5s ease-in 0.2s backwards;
}

#tail {
  animation: slidein 0.5s ease-in 0.6s backwards;
}

@keyframes slidein {
  from {
    opacity: 0;
    transform: translate3d(0, -25px, 0);
    visibility: visible;
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

#avatar img {
  width: 160px;
  height: 160px;
  border-radius: 80px;
}

#title {
  font-size: 2rem;
  font-weight: 300;
}

#all {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  padding-right: 1rem;
  padding-left: 1rem;
}

@media screen and (min-height: 800px) {
  #content {
    padding-bottom: 120px;
  }
}

#email {
  word-break: break-all;
  font-size: 1.8rem;
}

#email a {
  color: #5aa3b5;
  text-decoration: none;
}

.links a {
  border: 0;
  padding: 0.5rem;
  color: black;
  text-decoration: none;
}

.links a img,
.links a svg {
  height: 3rem;
  width: 3rem;
}

#github-i {
  fill: #1b1f23;
}

#devto-i {
  fill: black;
}

/* Dark mode */
@media (prefers-color-scheme: dark) {
  body {
    background-color: #2d2b29;
    color: white;
  }

  #github-i {
    fill: white;
  }

  #devto-i {
    fill: white;
  }
}
